import React, { useMemo } from "react";
import { Table, Container } from "reactstrap";

const StateTable = (props) => {
  const storeData = useMemo(() => props.stateData, [props.stateData]);
  const filteredData = storeData.filter(
    (item, index) => item.state !== "Total"
  );

  return (
    <div className="container-fluid">
      <Table striped responsive className="mb-5">
        <thead>
          <tr>
            <th>State</th>
            <th>Confirmed</th>
            <th>Active</th>
            <th>Deaths</th>
            <th>Recovered</th>
            <th>Total Tests</th>
          </tr>
        </thead>
        <tbody>
          {filteredData
            ? filteredData.map((dt) => (
                <tr key={dt.state}>
                  <td>{dt.state}</td>
                  <td>{dt.confirmed}</td>
                  <td>{dt.active}</td>
                  <td>{dt.deaths}</td>
                  <td>{dt.recovered}</td>
                  <td>{dt.totaltested ? dt.totaltested : "-"}</td>
                </tr>
              ))
            : null}
        </tbody>
      </Table>
    </div>
  );
};
export default StateTable;
